import { vec2, vec3 } from "gl-matrix";
import Scene from "../Engine/Rendering/Scene";
import PositionComponent from "../Engine/ECS/Components/PositionComponent";
import MovementComponent from "../Engine/ECS/Components/MovementComponent";
import Entity from "../Engine/ECS/Entity";
import BoundingBoxComponent from "../Engine/ECS/Components/BoundingBoxComponent";
import { input } from "./GameMachine";
import Game from "./States/Game";
import { ECSUtils } from "../Engine/Utils/ESCUtils";
import { OverlayRendering } from "../Engine/Rendering/GUI/OverlayRendering";
import TextObject2D from "../Engine/Rendering/GUI/Objects/Text/TextObject2D";
import MeshCollisionComponent from "../Engine/ECS/Components/MeshCollisionComponent";
import MeshStore from "../Engine/AssetHandling/MeshStore";
import GraphicsComponent from "../Engine/ECS/Components/GraphicsComponent";
import Ray from "../Engine/Physics/Shapes/Ray";
import { ComponentTypeEnum } from "../Engine/ECS/Components/Component";
import { StatesEnum } from "../Engine/States/State";

const AllCollisionEntities = "Box || Box Pink || Box Gray || Box Green || Diamond || Sphere || Museum || Police car";
const OnlyMovingCollisionEntities = "Moving Box"; // Doesn't actually exist yet

const BallScaleFactor = 0.5;

export default class GolfBall {
    private scene: Scene;
	private game: Game;
	private meshStore: MeshStore;

    private entity: Entity;
	private positionComp: PositionComponent;
    private movComp: MovementComponent;

	private previewEntity: Entity;
	private previewPositionComp: PositionComponent;

    private mouseWasPressed: boolean;
	private mouseRightWasPressed: boolean;

	private mouseMovement: vec2;
	private jawPitch: vec2;

	private direction: vec3;
	private nextDirection: vec3;
	private nextBounceVec: vec3;
	private nextBounceTime: number;
	private nextBounceEntityId: number;
	private lastBounceTime: number;
	private bounceLimit: number;
	private currentBounces: number;
	private bounceLimitDisplay: TextObject2D;

    constructor(scene: Scene, game: Game, overlayRendering: OverlayRendering, meshStore: MeshStore) {
        this.scene = scene;
		this.game = game;
		this.meshStore = meshStore;

		this.bounceLimitDisplay = overlayRendering.getNew2DText();
		vec2.set(this.bounceLimitDisplay.position, 0.5, 0.05);
		this.bounceLimitDisplay.center = true;
		this.bounceLimitDisplay.textString = "1";

		this.jawPitch = vec2.create();
		this.mouseMovement = vec2.create();
		input.mouseMoveCallBack = (event: MouseEvent) => {
			if (input.mouseRightClicked) {
				let movX = event.movementX;
				let movY = event.movementY;

				if (Math.abs(movX) > window.innerWidth * 0.3) {
					movX = 0.0;
				}

				if (Math.abs(movY) > window.innerHeight * 0.3) {
					movY = 0.0;
				}
				
				vec2.sub(this.mouseMovement, this.mouseMovement, vec2.fromValues(movX, movY));
			}
		}

		this.direction = vec3.create();
		this.nextDirection = vec3.create();
		this.nextBounceVec = vec3.create();
		this.nextBounceTime = Infinity;
		this.nextBounceEntityId = -1;
		this.lastBounceTime = 0.0;
		this.bounceLimit = 1;
		
		this.mouseWasPressed = true;
		this.mouseRightWasPressed = true;

		this.positionComp = new PositionComponent();
		vec3.set(this.positionComp.scale, BallScaleFactor, BallScaleFactor, BallScaleFactor);
        this.entity = this.game.ecsManager.createEntity();
		this.game.ecsManager.addComponent(this.entity, this.positionComp);
        let boundingBoxComp = new BoundingBoxComponent();
		boundingBoxComp.boundingBox.setMinAndMaxVectors(
			vec3.fromValues(-1.0, -1.0, -1.0),
			vec3.fromValues(1.0, 1.0, 1.0)
		);
		boundingBoxComp.updateTransformMatrix(this.positionComp.matrix);
		this.game.ecsManager.addComponent(this.entity, boundingBoxComp);

        // let collisionComp = new CollisionComponent();
		// collisionComp.mass = 20.0;
		// collisionComp.frictionCoefficient = 1.0;
		// collisionComp.collisionCoefficient = 0.0;
		// this.game.ecsManager.addComponent(this.entity, collisionComp);

		this.movComp = new MovementComponent();
		this.movComp.drag = 0.0;
        vec3.zero(this.movComp.constantAcceleration);
		this.game.ecsManager.addComponent(this.entity, this.movComp);

		this.previewEntity = this.game.ecsManager.createEntity();
		this.previewPositionComp = new PositionComponent();
		vec3.set(this.previewPositionComp.scale, 0.0, 0.0, 0.0);
		this.game.ecsManager.addComponent(this.previewEntity, this.previewPositionComp);
    }

	init () {
		if (!this.previewEntity.hasComponent(ComponentTypeEnum.GRAPHICS)) {
			this.game.ecsManager.addComponent(this.previewEntity, new GraphicsComponent(
				this.scene.getNewMesh("Assets/objs/sphere.obj", "CSS:rgba(255, 255, 255, 0.5)", "Assets/objs/sphere_spec.mtl")
			));
		}

		if (!this.entity.hasComponent(ComponentTypeEnum.MESHCOLLISION)) {
			let octree = this.meshStore.getOctree("Assets/objs/sphere.obj", false);
			if (octree != undefined) {
				let meshColComp = new MeshCollisionComponent(octree);
				this.game.ecsManager.addComponent(this.entity, meshColComp);
			}
		}
		this.respawn();
	}

    respawn() {
		vec3.set(this.positionComp.position, 0.0, -1.5, 0.0);
		vec3.set(this.movComp.velocity, 0.0, 0.0, 0.0);
		this.setCameraDirection(vec3.fromValues(-1.0, 0.0, 0.0));
		vec2.zero(this.mouseMovement);
		
		vec3.zero(this.direction);
		vec3.zero(this.nextDirection);
		vec3.zero(this.nextBounceVec);
		vec3.zero(this.previewPositionComp.scale);
		this.nextBounceTime = Infinity;
		this.lastBounceTime = 0.0;
		this.nextBounceEntityId = -1;
		this.currentBounces = 0;
	}

	private saveNextCollisionInfo(continousCollisionInfo: {time: number; eId: number; intersectionVec: vec3;}) {
		this.nextBounceTime = continousCollisionInfo.time;
		if (continousCollisionInfo.intersectionVec == undefined || vec3.squaredLength(continousCollisionInfo.intersectionVec) == 0.0) {
			if (this.nextBounceTime != Infinity) {
				vec3.normalize(this.nextBounceVec, vec3.scale(this.nextBounceVec, this.direction, -1.0));
			}
		}
		else {
			vec3.copy(this.nextBounceVec, continousCollisionInfo.intersectionVec);
		}
		this.nextBounceEntityId = continousCollisionInfo.eId;

		// Calculate next direction
		if (vec3.equals(this.nextBounceVec, vec3.fromValues(0, 0, 0)) || this.game.objectPlacer.getTypeOfEntity(this.nextBounceEntityId) == "Box Green") {
			// Can't calculate next, or the next one is the finish. Let's use the current direction 
			vec3.copy(this.nextDirection, this.direction);
		}
		else {
			let dot = vec3.dot(this.direction, this.nextBounceVec);
			vec3.scaleAndAdd(this.nextDirection, this.direction, this.nextBounceVec, 2.0 * -dot);
		}

		// console.log(this.nextDirection);
		// console.log(this.nextBounceTime);
	}

	private setCameraDirection(direction: vec3) {
		let newDir = vec3.clone(direction);
		vec3.normalize(newDir, newDir);

		this.game.rendering.camera.setDir(newDir);
		this.jawPitch[1] = Math.asin(newDir[1]) * 180 / Math.PI;
		this.jawPitch[0] = Math.atan2(newDir[0], newDir[2]) * 180 / Math.PI;
	}

	/**
	 * This function does custom movement updates, not relying on the movement system (until not on collision path with anything, then the movement will be set to keep going).
	 * TODO: Consider breaking this logic out to some system.
	 * @param dt Time elapsed since last frame
	 * @returns how much is left to process of the dt
	 */
	doMovementAndBounces(dt: number): number {
		let dtLeft = 0.0;

		if (input.mouseClicked) {
            if (!this.mouseWasPressed && vec3.squaredLength(this.direction) == 0.0) {
				this.currentBounces = 0;
				vec3.scale(this.direction, this.game.rendering.camera.getDir(), 40.0);
				vec3.zero(this.movComp.velocity);
            }
			this.mouseWasPressed = true;
        }
        else {
            this.mouseWasPressed = false;
        }

		if (vec3.squaredLength(this.direction) > 0.0) {
			if (this.nextBounceTime == Infinity) {
				let entities = this.game.objectPlacer.getEntitiesOfType(AllCollisionEntities); // All entities that should be collidable
				let continousCollisionInfo = ECSUtils.CalculateCollisionTime(this.entity, this.direction, entities, Infinity, false);
				
				this.saveNextCollisionInfo(continousCollisionInfo);
			}
			else {
				let entitiesToGet = OnlyMovingCollisionEntities; // Only entities that will move and change velocity over time
				if (this.lastBounceTime < 0.05) {
					entitiesToGet = AllCollisionEntities; // Sometimes next bounce gets messed up just in the beginning after bouncing, don't know why. This will update it correctly I hope.
				}
				let entities = this.game.objectPlacer.getEntitiesOfType(entitiesToGet); 
				let continousCollisionInfo = ECSUtils.CalculateCollisionTime(this.entity, this.direction, entities, this.nextBounceTime, false);
				
				if (continousCollisionInfo.time < this.nextBounceTime) {
					this.saveNextCollisionInfo(continousCollisionInfo);
				}
			}

			if (this.nextBounceTime != Infinity) {
				let moveTime = Math.min(dt, this.nextBounceTime);
				vec3.scaleAndAdd(this.positionComp.position, this.positionComp.position, this.direction, moveTime);
				this.lastBounceTime += moveTime;
				this.nextBounceTime -= moveTime;
				dtLeft = dt - moveTime;

				if (this.nextBounceTime <= 0.000001) {
					if (this.game.objectPlacer.getTypeOfEntity(this.nextBounceEntityId) == "Box Green") {
						// Finish reached!
						// Stop the ball completely
						vec3.zero(this.direction);
						this.currentBounces = 0;
						// this.game.gotoState = StatesEnum.LEVELSELECT;
					}
					else if (this.currentBounces >= this.bounceLimit) {
						this.setCameraDirection(this.nextDirection); // Set the camera to look in the direction of the next bounce
						// Stop the ball completely
						vec3.zero(this.direction);
						this.currentBounces = 0;
					}
					else {
						// Reflect the movement vector along the intersectionVector
						vec3.copy(this.direction, this.nextDirection);
						this.currentBounces++;
					}
					this.lastBounceTime = 0.0;
					this.nextBounceTime = Infinity;
				}
			}
			else {
				vec3.copy(this.movComp.velocity, this.direction);
				vec3.zero(this.direction);
				this.lastBounceTime = 0.0;
				this.nextBounceTime = Infinity;
			}
		}

		return dtLeft;
	}

    update(dt: number) {
		let rightMouseClickedThisFrame = false;
		if (input.mouseRightClicked) {
			if (!this.mouseRightWasPressed) {
				document.getElementById("gameDiv").requestPointerLock();
				rightMouseClickedThisFrame = true;
			}
			this.mouseRightWasPressed = true;
		}
		else {
			if (this.mouseRightWasPressed) {
				document.exitPointerLock();
			}
			this.mouseRightWasPressed = false;
		}

		let movedMouse = false;

		if (vec2.squaredLength(this.mouseMovement) > 0.0) {
			let sensitivity: number = 45.0; // TODO: move to options, and add a slider in options menu.
			vec2.scaleAndAdd(this.jawPitch, this.jawPitch, this.mouseMovement, sensitivity * dt);
			this.jawPitch[0] = this.jawPitch[0] % 360;
			this.jawPitch[1] = Math.max(Math.min(this.jawPitch[1], 89), -89);
			this.game.rendering.camera.setPitchJawDegrees(this.jawPitch[1], this.jawPitch[0]);
			
			vec2.zero(this.mouseMovement);
			movedMouse = true;
		}
		else if (!input.mouseRightClicked && vec3.squaredLength(this.direction) > 0.0) {
			// Linear interpolation between direction and next direction when there is less than turnTime left to impact.
			const turnTime = 0.2;
			let newDir = vec3.lerp(vec3.create(), this.direction, this.nextDirection, Math.max(0.0, 1.0 - (this.nextBounceTime / Math.min(turnTime, this.lastBounceTime + this.nextBounceTime))));
			this.setCameraDirection(newDir);
		}

		if (input.mouseRightClicked) {
			if (movedMouse || rightMouseClickedThisFrame) {
				let entities = this.game.objectPlacer.getEntitiesOfType(AllCollisionEntities); // All entities that should be collidable
				// First do a ray cast to get a maximum distance
				let ray = new Ray();
				const dir = this.game.rendering.camera.getDir();
				ray.setStartAndDir(this.positionComp.position, dir);
				const distAndEid = ECSUtils.RayCastAgainstEntityList(ray, entities);
				let continousCollisionInfo = ECSUtils.CalculateCollisionTime(this.entity, dir, entities, distAndEid.distance, false);
				if (continousCollisionInfo.time < Infinity) {
					vec3.scaleAndAdd(this.previewPositionComp.position, this.positionComp.position, dir, continousCollisionInfo.time);
					vec3.set(this.previewPositionComp.scale, BallScaleFactor, BallScaleFactor, BallScaleFactor);
				}
				else {
					vec3.zero(this.previewPositionComp.scale);
				}
			}
		}
		else {
			vec3.zero(this.previewPositionComp.scale);
		}
		
		let dtLeft = dt;
		while (dtLeft > 0.0) {
			dtLeft = this.doMovementAndBounces(dtLeft);
		}

        if (input.keys[" "]) {
            vec3.zero(this.movComp.velocity);
			vec3.zero(this.direction);
			this.lastBounceTime = 0.0;
			this.nextBounceTime = Infinity;
        }

		if (input.keys["P"]) {
            this.respawn();
        }

		for (let i = 0; i < 10; i++) {
			if (input.keys[i.toString()]) {
				this.bounceLimit = i;
			}
		}
		this.bounceLimitDisplay.textString = this.bounceLimit.toString();

        this.game.rendering.camera.setPosition(this.positionComp.position);
    }

}